.message-screen {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.message-screen .message {
    font-size: 32px;
    font-weight: lighter;
    color: #FFF;
    animation: blinkFade 1s infinite;
}

@media (max-width: 450px) {
    .message-screen .message {
        font-size: 24px;
    }
}